import React, {Component} from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin'

import {Upload,Select, message, Form, Input, Anchor,Row,Col,Breadcrumb,Button,Checkbox} from 'antd';
const FormItem = Form.Item;
const Search = Input.Search;
const Option = Select.Option;
const { TextArea } = Input;
import {HashRouter as Router,Route,Link,Redirect,Switch} from 'react-router-dom'
import {cookieUtil} from "utils/cookie.js"


import './style.scss';


import Zhineng from 'images/zhineng.png';
import Tongzhi from 'images/tongzhi.png';
import Qrcode from 'images/qrcode.png'



const formItemLayout = {
    labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
    },
    wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
    },
};
// const formItemLayout = {
//     labelCol: { span: 4 },
//     wrapperCol: { span: 8 },
// };
const formTailLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 8, offset: 4 },
};
class Content extends Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
        this.state = {
            checkNick: false,
            uploaded1: false,
            uploaded2: false,
            document: "",
            code: ""
        }
    }

    toggleLogin = () => {
        this.props.logFn();
    }

    getChainsList = () => {
        const data = this.props.chainsList;

        if(data && data.length) {
            return data
        }

    }

    tableView(data, id){
        console.log(id)
        this.props.showChainsTable(data, id);
    }

    goZhixng() {
        location.replace("/" + location.hash + "/perform")
    }

    hideShowButton = (index, types) => {
        this.props.showAndHide(index, types);
    }

    getContractShopList = () => {
        let data = this.props.contractShopList;

        return data;
    }

    check = () => {
        this.props.form.validateFields(
            (err) => {
                if (!err) {
                    console.info('success');
                }
            },
        );
    }
    handleChange = (e) => {
        this.setState({
            checkNick: e.target.checked,
        }, () => {
            this.props.form.validateFields(['nickname'], { force: true });
        });
    }

    onSubmit = (e) => {
        e.preventDefault();
        const {initial} = this.props;
        let user = "";
        if(cookieUtil.hasItem("user")) {
            user = cookieUtil.getItem("user");
        }else{
            location.replace("/#/platform/login")
            return;
        }

        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
            }else{
                message.error("");

                reuturn;
            }
        });
        // alert(this.props.form.getFieldsValue("version"))
        if(this.state.uploaded1 && this.state.uploaded2) {
            this.props.contractPromote(
                Object.assign({},{version: this.props.form.getFieldsValue().version},
                    {
                        document: this.state.document,
                        code: this.state.code,
                        id: initial.id
                    })
            )
        }else{
            if(!this.state.uploaded1) {
                message.error("");
            }else{
                message.error("");
            }

        }
    }

    getChainidOptions = () => {
        let data = this.props.getChainsData;

        data.map((item, index) => {
            return <Option key={index} value={item.chainid}>{item.chainid}</Option>
        })
    }

    checkName = (e) => {
        console.log(e.target.value)


    }

    componentDidMount() {
        const {initial} = this.props;
        this.props.form.setFields({
            name: {
                value: initial.name
            },
            version:{
                value: initial.version
            },
            description: {
                value: initial.description
            },
            price: {
                value: initial.price
            },
            chainid: {
                value: initial.chainid
            }
        });
    }

    render() {

        const { getFieldDecorator, getFieldsError, getFieldError, isFieldTouched } = this.props.form;
        const props = {
            name: 'test',
            action: '/loulan/upload',
            method: 'post',
            data: {
                openid: "18610270284"
            },
            onChange:(info) => {
                if (info.file.status !== 'uploading') {
                    console.log(info.file.response,);
                    this.setState({
                        document: info.file.response
                    })
                }
                if (info.file.status === 'done') {
                    message.success(`${info.file.name} file uploaded successfully`);
                    this.setState({
                        uploaded1: true
                    })
                } else if (info.file.status === 'error') {
                    message.error(`${info.file.name} file upload failed.`);
                }
            },
        };
        const props2 = {
            name: 'test',
            action: '/loulan/upload',
            method: 'post',
            data: {
                openid: "18610270284"
            },
            onChange:(info) => {
                if (info.file.status !== 'uploading') {
                    console.log(info.file, info.fileList);
                    this.setState({
                        code: info.file.response
                    })
                }
                if (info.file.status === 'done') {
                    message.success(`${info.file.name} file uploaded successfully`);
                    this.setState({
                        uploaded2: true
                    })
                } else if (info.file.status === 'error') {
                    message.error(`${info.file.name} file upload failed.`);
                }
            },
        };
        return (
            <Row type="flex" justify="center" className="upload-content">
                <Col className="top" span={16}>
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform/contract/develop"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform/contract/upload"></Breadcrumb.Item>
                    </Breadcrumb>

                    <h2 className="title"></h2>

                    <Row type="flex" justify="center" className="chains-intent">
                        <Col span={18} className="content">
                            <div className="form-control">
                                <form onSubmit={this.onSubmit}>
                                <FormItem {...formItemLayout} label="" >
                                    {getFieldDecorator('name', {
                                        rules: [{
                                            required: true,
                                            message: '',
                                        }],
                                    })(
                                        <Input className="item" placeholder="" disabled={true}  />
                                    )}
                                </FormItem>
                                <FormItem {...formItemLayout} label="">
                                    {getFieldDecorator('version', {
                                        rules: [{
                                            required: true,
                                            message: '',
                                        }],
                                    })(
                                        <Input className="item" placeholder=" V1.0.0" />
                                    )}
                                </FormItem>
                                <FormItem {...formItemLayout} label=""  >
                                    {getFieldDecorator('description', {
                                        rules: [{
                                            required: true,
                                            message: '',
                                        }],
                                    })(
                                        <TextArea maxLength={400}  rows={4} className="item textarea" disabled={true} placeholder="400" />
                                    )}
                                </FormItem>
                                <FormItem {...formItemLayout} label="">
                                    {getFieldDecorator('price', {
                                        rules: [{
                                            required: true,
                                            message: '',
                                        }],
                                    })(
                                        <Input className="item"  disabled={true} placeholder="" />
                                    )}
                                </FormItem>
                                <FormItem {...formItemLayout} label="">
                                    {getFieldDecorator('chainid', {
                                        rules: [
                                            { required: true, message: 'ID' },
                                        ],
                                    })(
                                        <Select placeholder="" disabled={true}>
                                            {
                                                this.props.getChainsData.map((item, index) => {
                                                    return <Option key={index} value={item.chainid}>{item.name}</Option>
                                                })
                                            }
                                        </Select>
                                    )}
                                </FormItem>
                                <FormItem {...formItemLayout} label="" required="true">
                                    <div className="item upload">
                                        <Upload {...props}><span className="upload-item"></span></Upload>
                                        <Upload {...props2}><span className="upload-item"></span></Upload>
                                    </div>
                                </FormItem>
                                <FormItem {...formTailLayout}>
                                    <Button type="primary" htmlType="submit"  className="button">
                                        
                                    </Button>
                                </FormItem>
                                </form>
                            </div>
                        </Col>
                    </Row>

                    <div className="rule">
                        <p>1.</p>
                        <p>2.</p>
                    </div>

                </Col>
            </Row>


        )
    }
}

export default  Form.create()(Content);


